<template>
    <div class="goods">
        <!--  -->
        <div class="menu-wrapper" ref="menuWrapper">
            <ul>
                <li
                    class="menu-item"
                    ref="menuList"
                    v-for="(item, index) in goods"
                    :key="index"
                    :class="{ current: currentIndex === index }"
                    @click="selectMenu(index, $event)"
                >
                    <span class="text border-1px">
                        {{ item.name }}
                    </span>
                </li>
            </ul>
        </div>

        <!--  -->
        <div class="foods-wrapper" ref="foodsWrapper">
            <ul>
                <li
                    class="food-list"
                    ref="foodList"
                    v-for="(item, index) in goods"
                    :key="index"
                >
                    <p class="title"> {{ item.name }} </p>
                    <van-row>
                        <van-col
                            span="8"
                            v-for="(goods, i) in item.goods"
                            :key="i"
                        >
                            
                            <!-- <lazy-component> -->
                                <van-image
                                width="50"
                                height="50"
                                :src="goods.img"
                                @click="toDetail(goods.id)"
                            />
                            <!-- </lazy-component> -->
                            <p class="name">{{ goods.name }}</p>
                        </van-col>
                    </van-row>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import BScroll from "better-scroll";
import axios from "axios";
export default {
    name: "test",
    data() {
        return {
            goods: [],
            listHeight: [],
            scrollY: 0,
            selectedFood: {}
        };
    },

    computed: {
        currentIndex() {
            for (let i = 0; i < this.listHeight.length; i++) {
                let height1 = this.listHeight[i];
                let height2 = this.listHeight[i + 1];
                if (
                    !height2 ||
                    (this.scrollY >= height1 && this.scrollY < height2)
                ) {
                    this._followScroll(i);
                    return i;
                }
            }

            return 0;
        }
    },

    created() {
        axios.get("/mock/data.json").then(res => {
            const data = res.data;
            this.goods = data;
            this.$nextTick(() => {
                this._initScroll();
                this._calculateHeight();
            });
        });
    },

    methods: {
        // 
        _initScroll() {
            this.meunScroll = new BScroll(this.$refs.menuWrapper, {
                click: true
            });

            this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
                click: true,
                probeType: 3
            });

            this.foodsScroll.on("scroll", pos => {
                if (pos.y <= 0) {
                    this.scrollY = Math.abs(Math.round(pos.y));
                }
            });
        },

        // 
        _calculateHeight() {
            let foodList = this.$refs.foodList;
            let height = 0;
            this.listHeight.push(height);

            for (let i = 0; i < foodList.length; i++) {
                let item = foodList[i];
                height += item.clientHeight;
                this.listHeight.push(height);
            }
        },

        // 
        _followScroll(index) {
            let menuList = this.$refs.menuList;
            let el = menuList[index];
            this.meunScroll.scrollToElement(el, 300, 0, -100);
        },

        // 
        selectMenu(index, event) {
            if (!event._constructed) {
                return;
            }
            let foodList = this.$refs.foodList;
            let el = foodList[index];
            this.foodsScroll.scrollToElement(el, 300);
        },

        // 
        toDetail(params) {
            this.$router.push({ path: 'detail', query: { id: params }});
        }
    }
};
</script>


<style lang="scss" scoped>
.goods {
    display: flex;
    position: absolute;
    top: 46px;
    bottom: 50px;
    width: 100%;
    overflow: hidden;

    .menu-wrapper {
        background-color: #f3f5f7;
        flex: 3;

        .menu-item {
            height: 30px;
            line-height: 30px;
            text-align: center;
            padding: 0 12px;
            font-size: 10px;

            &.current {
                position: relative;
                z-index: 10;
                margin-top: -1px;
                color: #f76968;
                font-size: 13px;
            }

            .text {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
        }
    }

    .foods-wrapper {
        flex: 8;
        text-align: center;
        background-color: #fff;

        .title {
            margin-top: 20px;
            line-height: 30px;
            font-size: 14px;
            color: #333;
        }

        .name {
            line-height: 14px;
            font-size: 10px;
            color: #666;
        }
        .food-item {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}
</style>
